@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Ionicons:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=IcoMoon-Free:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Serif+Pro:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-source-sans-pro: "Source Sans Pro";
  --font-open-sans: "Open Sans";
  --font-ionicons: Ionicons;
  --font-source-serif-pro: "Source Serif Pro";
  --font-oswald: Oswald;
  --font-source-code-pro: "Source Code Pro";
  --font-icomoon-free: IcoMoon-Free;

  /* font sizes */
  --font-size-25xl: 44px;
  --font-size-base: 16px;
  --font-size-lg: 18px;
  --font-size-41xl: 60px;
  --font-size-6xl: 25px;
  --font-size-xl: 20px;
  --font-size-9xl: 28px;
  --font-size-17xl: 36px;
  --font-size-7xl: 26px;
  --font-size-sm: 14px;
  --font-size-26xl: 45px;
  --font-size-3xs: 10px;
  --font-size-2xl: 21px;
  --font-size-29xl: 48px;
  --font-size-11xl: 30px;
  --font-size-86xl: 105px;
  --font-size-53xl: 72px;

  /* Colors */
  --color-white: #fff;
  --color-orange-100: #fba820;
  --color-orange-200: #fca311;
  --color-whitesmoke-100: #f9f9f9;
  --color-whitesmoke-200: #f6f6f6;
  --color-whitesmoke-300: #f5f5f5;
  --color-whitesmoke-400: #ececec;
  --color-whitesmoke-500: #ebebeb;
  --color-whitesmoke-600: #eaeaea;
  --color-gray-100: #fdfdfd;
  --color-gray-200: #939393;
  --color-gray-300: #929292;
  --color-gray-400: #868686;
  --color-gray-500: #818181;
  --color-gray-600: #252525;
  --color-gray-700: rgba(255, 255, 255, 0.6);
  --color-gray-800: rgba(0, 0, 0, 0.22);
  --color-darkslategray-100: #3f3f3f;
  --color-darkslategray-200: #3e3e3e;
  --color-dimgray-100: #535353;
  --color-gainsboro-100: #e5e5e5;
  --color-gainsboro-200: #d9d9d9;
  --color-darkgray-100: #ababab;
  --color-darkgray-200: #a8a8a8;
  --color-lightgray: #d6d6d6;

  /* Paddings */
  --padding-mid: 17px;
  --padding-21xl: 40px;

  /* border radiuses */
  --br-12xs: 1px;
  --br-8xs: 5px;
  --br-3xs: 10px;
  --br-11xl: 30px;
  --br-21xl: 40px;
}
